/* src/components/ImageInfo.css */

.image-info {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px;
    cursor: text;
    font-weight: bold;
}

@media (max-width: 600px) {
    .image-info {
        left: auto;   
        right: 20px;  
        transform: none; 
        max-width: 40vw;
        text-align: right;
    }
}
