.carousel-container {
    position: relative;
    width: 100%;
    height: calc(100vh - 0px); /* Adjust height to account for any fixed header */
    overflow: hidden;
    cursor: default;
    display: flex;
    justify-content: center;
    align-items: center;
}

.carousel-slide {
    display: none;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 0; /* Remove padding to avoid shifting */
}

.carousel-slide.active {
    display: flex;
}

.carousel-slide img {
    max-height: 70vh;
    max-width: 100%;
    object-fit: contain;
    user-select: none;
    /* height: auto; */ /* Remove height 100% if causing issues */
}

/* New styles for the overlay divs */
.overlay {
    position: absolute;
    bottom: 0;
    width: 50%;
    cursor: pointer;
}

.left-overlay {
    left: 0;
}

.right-overlay {
    right: 0;
}

/* Styles for the counter */
.carousel-counter {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    padding: 5px 10px;
    border-radius: 5px;
    bottom: 20px;
    font-size: 16px;
}
