.info-container {
    padding: 20px;
    margin: 25px auto 0 auto; 
    max-width: 600px; 
    font-weight: bold;
    font-size: 24px;
    text-align: center;
    overflow: hidden;

}

.projects-list {
    margin-bottom: 40px;
}

.category {
    margin-bottom: 20px;
}

.category li {
    text-decoration: underline;
}

h4 {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: -20px;
}

ul {
    padding-left: 0px;
    list-style-type: none;
}

ul li {
    list-style-type: none;
}

.cv {
    text-align: left;
}
