/* src/components/Shop.css */

.shop-container {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    padding: 20px;
    margin-top: 150px;
}

.product {
    display: flex;
    flex-direction: row;
    margin-bottom: 40px;
}

.product-description {
    padding-right: 20px;
    width: 300px;
}

.product-description h1 {
    margin: 0;
    font-size: 16px;
    margin-bottom: 16px;
}

.product-description p {
    margin-bottom: 16px;
}

.product-description a {
    color: black;
    text-decoration: none;
}

.product-description a:hover {
    color: red;
}

.product-images {
    flex: 1;
    display: flex;
    gap: 10px;
    flex-wrap: wrap; 
}

.product-images img {
    max-width: 500px; 
    width: 100%; 
    height: auto; 
}